/* Ensure the App takes up the full height and width of the viewport */
.App {
  height: 100vh; /* 100% of the viewport height */
  width: 100vw;  /* 100% of the viewport width */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.title {
  font-size: 5vw;
  text-shadow: 2px 2px 4px rgba(255, 255, 255, 1); /* Horizontal offset, Vertical offset, Blur radius, and Color */
}

.colorName {
  border: 1px solid black;
  padding: 10px;
  border-radius: 5px;
  margin: 0 20px;
  width: 200px;
  height: 50px;
  background-color: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1vw; /* Dynamically adjust based on viewport width */
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}





.row {
  display: flex;  /* Flexbox for horizontal layout */
  justify-content: center;
  align-items: center; /* Center items vertically */
  margin-top:50px;
}

.box {
   /* Add some space between the buttons */
  flex-shrink: 0;  /* Prevent the buttons from shrinking */
}


@media (max-width: 400px) {
  .colorName {
    font-size: 1.5rem;   /* Reduce font size for smaller screens */
  }
}

